* {
    padding: 0;
    margin: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    /* -ms-scroll-chaining:chained; */
  }
  body {
    width: 100vw;
    height:100vh;
    overflow:hidden;
    -ms-scroll-chaining:chained;
  }
  .demo8 {
      position: absolute;
    width: 80%;
    height:80vh;
    top:10%;
    overflow-y:scroll;
    overflow-x: hidden
    /* -ms-scroll-chaining:chained; */
  }
  .demo8-item {
    /* -ms-scroll-chaining:chained; */
    position: absolute;
    width:100%;
    pointer-events: auto;
    transform-origin: 50% 50% 0px;
    border-radius: 0px;
    color: rgb(153, 255, 153);
    line-height: 48px;
    padding-left: 32px;
    font-size: 24px;
    font-weight: 400;
    background-color: rgb(200, 200, 40);
}
